import * as React from 'react';
import { Grid, Box, Typography, styled, Paper } from '@mui/material';

const title = {
    fontFamily: 'Inter',
    fontSize: '20px',
    fontStyled: 'normal',
    lineHeight: '1.5em',
    fontWeight: '600',
    marginTop: '20px',
    textAlign: 'left'
}
const text = {
    fontFamily: 'Inter',
    fontSize: '16px',
    fontStyled: 'normal',
    lineHeight: '1.5em',
    fontWeight: '600',
    marginTop: "20px",
    textAlign: 'left'
}
const date = {
    fontFamily: 'Inter',
    fontSize: '12px',
    fontStyled: 'normal',
    lineHeight: '1.5em',
    fontWeight: '600',
    marginTop: "20px",
    textAlign: 'left'
}
const Item = styled(Paper)(({ theme }) => ({
    backgroundColor: theme.palette.mode === 'dark' ? '#1A2027' : '#fff',
    ...theme.typography.body2,
    padding: theme.spacing(1),
    textAlign: 'center',
    color: theme.palette.text.secondary,
    boxShadow: 'none'
}));
const Img = styled('img')({
    width: '100%',
    borderRadius: '40px',
    cursor: 'pointer',
    '&:hover': {
        transform: 'scale(1.05)',
        transitionDuration: '0.3s'
    }


})

const Late = ({ items }) => {
    return (
        <>
            <Box sx={{ paddingTop: "10px", marginLeft: '50px', marginRight: '50px' }}>
                <Grid container rowSpacing={2} columnSpacing={{ xs: 1, sm: 2, md: 3 }} >
                    {
                        items.map((item) =>
                            <Grid item xs={12} md={6} lg={3} >
                                <Item sx={{ display: 'flex', flexDirection: 'column' }}>
                                    <a href={item.url}><Img src={item.img}></Img></a>

                                    <Typography sx={title}>{item.title}</Typography>
                                    <Typography sx={text}>{item.text}</Typography>
                                </Item>
                            </Grid>
                        )
                    }
                </Grid>
            </Box>
        </>
    )
}
export default Late;