import { slide as Menu } from "react-burger-menu";


const SideBar = (props) => {
    return (
        <Menu {...props}>
            <a href='/value' className="menu-item" >
                Home
            </a>

            <a href='/games' className="menu-item" >
                Products
            </a>

            <a href='/latest' className="menu-item" >
                Team
            </a>

        </Menu>
    )
}
export default SideBar