import * as React from 'react';
import { Box, Typography } from '@mui/material';
import '../../../src/global.css';
import { Link, animateScroll as scroll } from 'react-scroll';
import ExpandMoreIcon from '@mui/icons-material/ExpandMore';
import { Fade } from 'react-awesome-reveal';


const title = {
    fontFamily: 'Inter',
    fontSize: { xs: '30px', md: '35px', lg: "38px", xl: '68px' },
    fontStyled: 'normal',
    lineHeight: '1.5em',
    fontWeight: '400',
    color: "white",
    textAlign: 'center',
    marginTop: "-23px"

}
const text = {
    fontFamily: 'Inter',
    fontSize: { xs: '18px', md: '20px', lg: "25px", xl: '35px' },
    fontStyled: 'normal',
    lineHeight: '1.5em',
    fontWeight: '400',
    color: "white",
    textAlign: 'center',
}
const First = () => {
    return (
        <>
            <Box >
                <div className='img9'><img src='./assets/images/39.png' alt='background' className='img1'></img></div>
                <Box sx={{ display: 'flex', justifyContent: 'center', alignItems: 'center', flexDirection: 'column', marginTop: '-30%', marginRight: '10%', marginLeft: "10%" }}>
                    <Fade direction='left' duration={1000}><Typography sx={title}>Our vision?</Typography></Fade>
                    <Fade direction='right' duration={1000}>
                        <Typography sx={text}>Bluesoft is a game development agent company that aims to share the passion for creative and outstanding games.</Typography></Fade>
                </Box>
                <div className='icon'>
                    <Link to='dice' spy={true} smooth={true} duration={500} offset={-80} style={{ cursor: 'pointer' }}><ExpandMoreIcon sx={{ width: { xs: '80px', sm: '200px', }, height: { xs: '80px', sm: '200px' }, color: 'white' }} className='icons' /></Link>
                </div>
            </Box>
        </>
    )
}
export default First;