import * as React from 'react';
import SideBar from './sidebar';
import { Box, Stack, Typography, } from '@mui/material';
import '../../../src/global.css';
import { Link } from 'react-router-dom';
import '../../../src/global.css';

const text = {
    fontFamily: 'IceBerg',
    fontSize: '25px',
    fontWeight: '700',
    letterSpacing: '2px',
    fontStyle: 'normal',
    color: 'black',
    '&:hover': {
        transitionDuration: '0.2s',
        transform: 'scale(1.05)',
        color: '#E7B811'
    }

}
const name = {
    fontFamily: 'Inter',
    fontSize: '18px',
    fontWeight: '700',
    fontStyle: 'normal',
    color: 'black',
    marginLeft: "-18%"
}
const Header = () => {
    return (
        <>
            <Box sx={{ display: 'block', position: 'fixed', width: '100%', background: 'rgba(255, 255, 255, 0.8)', boxShadow: 'rgb(0 0 0 / 30%) 0px 0px 3px', zIndex: '1000000' }}>
                <Box sx={{ display: 'flex', justifyContent: 'space-between', margin: '30px', alignItems: 'center' }}>
                    <Link to='/'><img src='./assets/images/logo1.png' alt='logo' className='logo'></img></Link>

                    <Stack direction='row' spacing={10} className='header' sx={{ marginRight: "35%" }}>
                        <Link to='/' className='deco' ><Typography sx={text}>Home</Typography></Link>
                        <Link to='/games' className='deco' ><Typography sx={text}>Products</Typography></Link>
                        <Link to='/latest' className='deco' ><Typography sx={text}>Team</Typography></Link>
                    </Stack>

                </Box>
                <SideBar pageWrapId={"page-wrap"} outerContainerId={"App"} />
            </Box>
        </>
    )

}
export default Header;